.chatWindow {
   display: flex;
   flex-direction: column;
   height: 100%;
}

.chatWindow-header {
   height: 60px;
   border-bottom: 1px solid #ccc;
   display: flex;
   justify-content: space-between;
   align-items: center;
}

.chatWindow-headerinfo {
   display: flex;
   align-items: center;
   cursor: pointer;
}

.chatWindow-avatar {
   width: 40px;
   height: 40px;
   border-radius: 50%;
   margin-left: 15px;
   margin-right: 15px;
}

.chatWindow-name {
   font-size: 17px;
   color: #000;
}

.chatWindow-headerbuttons {
   display: flex;
   align-items: center;
   margin-right: 15px;
}

.chatWindow-btn {
   width: 40px;
   height: 40px;
   border-radius: 50%;
   display: flex;
   justify-content: center;
   align-items: center;
   cursor: pointer;
}

.chatWindow-body {
   flex: 1;
   overflow-y: auto;
   background-color: #e5ddd5;
   background-size: cover;
   background-position: center;
   background-image: url("https://i.pinimg.com/originals/62/40/b6/6240b66d5c50c8661eee78b439a7d33c.jpg");
}

.chatWindow-footer {
   height: 62px;
   display: flex;
   align-items: center;
   background-color: #dbd4d4;
}

.chatWindow-pre {
   display: flex;
   margin: 0 15px;
}

.chatWindow-inputarea {
   flex: 1;
}

.chatWindow-input {
   width: 100%;
   height: 35px;
   border: 0;
   outline: 0;
   background-color: #fff;
   border-radius: 20px;
   font-size: 15px;
   color: #4a4a4a;
   padding-left: 15px;
}

.chatWindow-pos {
   display: flex;
   margin: 0 15px;
}

.chatWindow-emojiarea {
   height: 200px;
   overflow-y: hidden;
   transition: all ease 0.3s;
}

aside.emoji-picker-react {
   width: auto;
   background-color: none;
}

.emoji-picker-react .emoji-group:before {
   background: none;
}
